'use client';

import { useEffect, useState } from 'react';
import { useRouter } from 'next/navigation';
import { DashboardNav } from '@/components/dashboard-nav';
import { ProjectForm } from '@/components/project-form';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Dialog, DialogContent, DialogHeader, DialogTitle } from '@/components/ui/dialog';
import { authService } from '@/lib/auth';
import { projectsStorage, clientsStorage } from '@/lib/storage';
import { Project, Client } from '@/types';
import { formatCLP } from '@/lib/utils';
import { toast } from 'sonner';

// Extended type for projects with client details
type ProjectWithClient = Project & {
  client?: Client;
};

export default function ProjectsPage() {
  const router = useRouter();
  const [projects, setProjects] = useState<ProjectWithClient[]>([]);
  const [clients, setClients] = useState<Client[]>([]);
  const [searchTerm, setSearchTerm] = useState('');
  const [statusFilter, setStatusFilter] = useState<string>('all');
  const [isLoading, setIsLoading] = useState(true);
  const [showForm, setShowForm] = useState(false);
  const [editingProject, setEditingProject] = useState<Project | null>(null);

  useEffect(() => {
    // Check authentication
    if (!authService.isAuthenticated()) {
      router.push('/login');
      return;
    }

    loadProjects();
  }, [router]);

  const loadProjects = () => {
    try {
      const projectsData = projectsStorage.load();
      const clientsData = clientsStorage.load();
      
      // Add client info to projects
      const projectsWithClients = projectsData.map(project => ({
        ...project,
        client: clientsData.find(client => client.id === project.clientId)
      }));

      setProjects(projectsWithClients);
      setClients(clientsData);
    } catch (error) {
      console.error('Error loading projects:', error);
      toast.error('Error al cargar proyectos');
    } finally {
      setIsLoading(false);
    }
  };

  const filteredProjects = projects.filter(project => {
    const matchesSearch = 
      project.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
      project.description.toLowerCase().includes(searchTerm.toLowerCase()) ||
      project.client?.name.toLowerCase().includes(searchTerm.toLowerCase());
    
    const matchesStatus = statusFilter === 'all' || project.status === statusFilter;
    
    return matchesSearch && matchesStatus;
  });

  const getStatusBadge = (status: Project['status']) => {
    const statusConfig = {
      'pending': { label: 'Pendiente', variant: 'secondary' as const },
      'in-progress': { label: 'En Progreso', variant: 'default' as const },
      'completed': { label: 'Completado', variant: 'outline' as const },
      'cancelled': { label: 'Cancelado', variant: 'destructive' as const }
    };

    const config = statusConfig[status];
    return (
      <Badge 
        variant={config.variant}
        className={status === 'completed' ? 'bg-green-100 text-green-800 border-green-200' : ''}
      >
        {config.label}
      </Badge>
    );
  };

  const handleCreateProject = () => {
    if (clients.length === 0) {
      toast.error('Primero debes crear al menos un cliente');
      router.push('/dashboard/clients');
      return;
    }
    setEditingProject(null);
    setShowForm(true);
  };

  const handleEditProject = (project: Project) => {
    setEditingProject(project);
    setShowForm(true);
  };

  const handleSaveProject = (project: Project) => {
    loadProjects(); // Reload projects
    setShowForm(false);
    setEditingProject(null);
  };

  const handleDeleteProject = (projectId: string) => {
    if (confirm('¿Estás seguro de que quieres eliminar este proyecto?')) {
      try {
        const projects = projectsStorage.load();
        const updatedProjects = projects.filter(p => p.id !== projectId);
        projectsStorage.save(updatedProjects);
        loadProjects();
        toast.success('Proyecto eliminado exitosamente');
      } catch (error) {
        console.error('Error deleting project:', error);
        toast.error('Error al eliminar el proyecto');
      }
    }
  };

  const getProjectStats = () => {
    return {
      total: filteredProjects.length,
      pending: filteredProjects.filter(p => p.status === 'pending').length,
      inProgress: filteredProjects.filter(p => p.status === 'in-progress').length,
      completed: filteredProjects.filter(p => p.status === 'completed').length,
      cancelled: filteredProjects.filter(p => p.status === 'cancelled').length
    };
  };

  const stats = getProjectStats();

  if (isLoading) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="text-center">
          <div className="w-8 h-8 border-4 border-slate-200 border-t-slate-600 rounded-full animate-spin mx-auto mb-4"></div>
          <p className="text-slate-600">Cargando proyectos...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-slate-50">
      <DashboardNav />
      
      <div className="md:ml-64">
        <div className="p-6">
          {/* Header */}
          <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between mb-8">
            <div>
              <h1 className="text-3xl font-bold text-slate-900">Proyectos</h1>
              <p className="text-slate-600 mt-2">
                Gestiona tus trabajos y proyectos
              </p>
            </div>
            
            <Button onClick={handleCreateProject} className="mt-4 sm:mt-0">
              Nuevo Proyecto
            </Button>
          </div>

          {/* Stats Cards */}
          {projects.length > 0 && (
            <div className="grid grid-cols-2 md:grid-cols-5 gap-4 mb-6">
              <Card>
                <CardContent className="p-4">
                  <div className="text-sm font-medium text-slate-500">Total</div>
                  <div className="text-2xl font-bold text-slate-900">{stats.total}</div>
                </CardContent>
              </Card>
              <Card>
                <CardContent className="p-4">
                  <div className="text-sm font-medium text-slate-500">Pendientes</div>
                  <div className="text-2xl font-bold text-orange-600">{stats.pending}</div>
                </CardContent>
              </Card>
              <Card>
                <CardContent className="p-4">
                  <div className="text-sm font-medium text-slate-500">En Progreso</div>
                  <div className="text-2xl font-bold text-blue-600">{stats.inProgress}</div>
                </CardContent>
              </Card>
              <Card>
                <CardContent className="p-4">
                  <div className="text-sm font-medium text-slate-500">Completados</div>
                  <div className="text-2xl font-bold text-green-600">{stats.completed}</div>
                </CardContent>
              </Card>
              <Card>
                <CardContent className="p-4">
                  <div className="text-sm font-medium text-slate-500">Cancelados</div>
                  <div className="text-2xl font-bold text-red-600">{stats.cancelled}</div>
                </CardContent>
              </Card>
            </div>
          )}

          {/* Filters */}
          <div className="flex flex-col sm:flex-row gap-4 mb-6">
            <Input
              placeholder="Buscar proyectos por título, descripción o cliente..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              className="flex-1"
            />
            
            <Select value={statusFilter} onValueChange={setStatusFilter}>
              <SelectTrigger className="w-full sm:w-48">
                <SelectValue placeholder="Filtrar por estado" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="all">Todos los estados</SelectItem>
                <SelectItem value="pending">Pendiente</SelectItem>
                <SelectItem value="in-progress">En Progreso</SelectItem>
                <SelectItem value="completed">Completado</SelectItem>
                <SelectItem value="cancelled">Cancelado</SelectItem>
              </SelectContent>
            </Select>
          </div>

          {/* Projects List */}
          {filteredProjects.length === 0 ? (
            <Card>
              <CardContent className="flex flex-col items-center justify-center py-12">
                <div className="w-16 h-16 bg-slate-100 rounded-full flex items-center justify-center mb-4">
                  <div className="w-8 h-8 bg-slate-300 rounded"></div>
                </div>
                <h3 className="text-lg font-semibold text-slate-900 mb-2">
                  {searchTerm || statusFilter !== 'all' ? 'No se encontraron proyectos' : 'No tienes proyectos'}
                </h3>
                <p className="text-slate-500 text-center mb-4">
                  {searchTerm || statusFilter !== 'all' 
                    ? 'Intenta con otros términos de búsqueda o filtros'
                    : 'Comienza creando tu primer proyecto'
                  }
                </p>
                {!searchTerm && statusFilter === 'all' && (
                  <Button onClick={handleCreateProject}>
                    Crear Primer Proyecto
                  </Button>
                )}
              </CardContent>
            </Card>
          ) : (
            <div className="space-y-4">
              <p className="text-sm text-slate-600">
                {filteredProjects.length} proyecto{filteredProjects.length !== 1 ? 's' : ''}
              </p>
              
              <div className="grid gap-4">
                {filteredProjects.map((project) => (
                  <Card key={project.id} className="hover:shadow-md transition-shadow">
                    <CardContent className="p-6">
                      <div className="flex items-start justify-between mb-4">
                        <div className="flex-1">
                          <h3 className="text-lg font-semibold text-slate-900 mb-1">
                            {project.title}
                          </h3>
                          <p className="text-sm text-slate-600 mb-2">
                            Cliente: {project.client?.name || 'Sin cliente'}
                          </p>
                          <p className="text-sm text-slate-700 line-clamp-2">
                            {project.description}
                          </p>
                        </div>
                        <div className="flex items-center space-x-2 ml-4">
                          {getStatusBadge(project.status)}
                        </div>
                      </div>
                      
                      <div className="grid grid-cols-2 md:grid-cols-4 gap-4 text-sm mb-4">
                        <div>
                          <span className="text-slate-500">Inicio:</span>
                          <p className="font-medium">
                            {new Date(project.startDate).toLocaleDateString('es-CL')}
                          </p>
                        </div>
                        
                        {project.endDate && (
                          <div>
                            <span className="text-slate-500">Fin:</span>
                            <p className="font-medium">
                              {new Date(project.endDate).toLocaleDateString('es-CL')}
                            </p>
                          </div>
                        )}
                        
                        {project.budget && (
                          <div>
                            <span className="text-slate-500">Presupuesto:</span>
                            <p className="font-medium text-green-600">
                              {formatCLP(project.budget)}
                            </p>
                          </div>
                        )}
                        
                        {project.hoursWorked && (
                          <div>
                            <span className="text-slate-500">Horas:</span>
                            <p className="font-medium">
                              {project.hoursWorked}h
                            </p>
                          </div>
                        )}
                      </div>
                      
                      <div className="flex items-center justify-between pt-4 border-t border-slate-100">
                        <div className="text-xs text-slate-500">
                          Creado: {new Date(project.createdAt).toLocaleDateString('es-CL')}
                        </div>
                        
                        <div className="flex space-x-2">
                          <Button
                            variant="outline"
                            size="sm"
                            onClick={() => router.push(`/dashboard/projects/${project.id}`)}
                          >
                            Ver Detalles
                          </Button>
                          <Button
                            variant="outline"
                            size="sm"
                            onClick={() => handleEditProject(project)}
                          >
                            Editar
                          </Button>
                          <Button
                            variant="outline"
                            size="sm"
                            onClick={() => handleDeleteProject(project.id)}
                          >
                            Eliminar
                          </Button>
                        </div>
                      </div>
                    </CardContent>
                  </Card>
                ))}
              </div>
            </div>
          )}
        </div>
      </div>

      {/* Project Form Dialog */}
      <Dialog open={showForm} onOpenChange={setShowForm}>
        <DialogContent className="max-w-4xl max-h-[90vh] overflow-y-auto">
          <DialogHeader>
            <DialogTitle>
              {editingProject ? 'Editar Proyecto' : 'Nuevo Proyecto'}
            </DialogTitle>
          </DialogHeader>
          <ProjectForm
            project={editingProject || undefined}
            onSave={handleSaveProject}
            onCancel={() => setShowForm(false)}
          />
        </DialogContent>
      </Dialog>
    </div>
  );
}
