'use client';

import { useState, useEffect } from 'react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Textarea } from '@/components/ui/textarea';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Alert, AlertDescription } from '@/components/ui/alert';
import { Project, Client } from '@/types';
import { clientsStorage, projectsStorage, generateId } from '@/lib/storage';
import { formatCLP } from '@/lib/utils';
import { toast } from 'sonner';

interface ProjectFormProps {
  project?: Project;
  onSave: (project: Project) => void;
  onCancel: () => void;
}

export function ProjectForm({ project, onSave, onCancel }: ProjectFormProps) {
  const [clients, setClients] = useState<Client[]>([]);
  const [formData, setFormData] = useState({
    title: project?.title || '',
    description: project?.description || '',
    clientId: project?.clientId || '',
    status: project?.status || 'pending' as Project['status'],
    startDate: project?.startDate ? new Date(project.startDate).toISOString().split('T')[0] : '',
    endDate: project?.endDate ? new Date(project.endDate).toISOString().split('T')[0] : '',
    budget: project?.budget?.toString() || '',
    hoursWorked: project?.hoursWorked?.toString() || '',
    hourlyRate: project?.hourlyRate?.toString() || ''
  });
  const [errors, setErrors] = useState<Record<string, string>>({});
  const [isLoading, setIsLoading] = useState(false);

  useEffect(() => {
    // Load clients for selection
    const clientsData = clientsStorage.load();
    setClients(clientsData);
  }, []);

  const validateForm = () => {
    const newErrors: Record<string, string> = {};

    if (!formData.title.trim()) {
      newErrors.title = 'El título es requerido';
    }

    if (!formData.description.trim()) {
      newErrors.description = 'La descripción es requerida';
    }

    if (!formData.clientId) {
      newErrors.clientId = 'Debe seleccionar un cliente';
    }

    if (!formData.startDate) {
      newErrors.startDate = 'La fecha de inicio es requerida';
    }

    if (formData.endDate && formData.startDate && new Date(formData.endDate) < new Date(formData.startDate)) {
      newErrors.endDate = 'La fecha de fin debe ser posterior a la fecha de inicio';
    }

    if (formData.budget && (isNaN(Number(formData.budget)) || Number(formData.budget) < 0)) {
      newErrors.budget = 'El presupuesto debe ser un número válido';
    }

    if (formData.hoursWorked && (isNaN(Number(formData.hoursWorked)) || Number(formData.hoursWorked) < 0)) {
      newErrors.hoursWorked = 'Las horas trabajadas deben ser un número válido';
    }

    if (formData.hourlyRate && (isNaN(Number(formData.hourlyRate)) || Number(formData.hourlyRate) < 0)) {
      newErrors.hourlyRate = 'La tarifa por hora debe ser un número válido';
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!validateForm()) {
      return;
    }

    setIsLoading(true);

    try {
      const projectData: Project = {
        id: project?.id || generateId(),
        title: formData.title.trim(),
        description: formData.description.trim(),
        clientId: formData.clientId,
        status: formData.status,
        startDate: new Date(formData.startDate),
        endDate: formData.endDate ? new Date(formData.endDate) : undefined,
        budget: formData.budget ? Number(formData.budget) : undefined,
        hoursWorked: formData.hoursWorked ? Number(formData.hoursWorked) : undefined,
        hourlyRate: formData.hourlyRate ? Number(formData.hourlyRate) : undefined,
        createdAt: project?.createdAt || new Date(),
        updatedAt: new Date()
      };

      // Save to storage
      const projects = projectsStorage.load();
      if (project) {
        // Update existing project
        const index = projects.findIndex(p => p.id === project.id);
        if (index !== -1) {
          projects[index] = projectData;
        }
      } else {
        // Add new project
        projects.push(projectData);
      }
      
      projectsStorage.save(projects);
      
      toast.success(project ? 'Proyecto actualizado exitosamente' : 'Proyecto creado exitosamente');
      onSave(projectData);
    } catch (error) {
      console.error('Error saving project:', error);
      toast.error('Error al guardar el proyecto');
    } finally {
      setIsLoading(false);
    }
  };

  const handleInputChange = (field: string, value: string) => {
    setFormData(prev => ({ ...prev, [field]: value }));
    // Clear error when user starts typing
    if (errors[field]) {
      setErrors(prev => ({ ...prev, [field]: '' }));
    }
  };

  const calculateTotalValue = () => {
    const hours = Number(formData.hoursWorked) || 0;
    const rate = Number(formData.hourlyRate) || 0;
    return hours * rate;
  };

  return (
    <div className="w-full">
      <form onSubmit={handleSubmit} className="space-y-6">
          {/* Basic Information */}
          <div className="space-y-4">
            <div>
              <Label htmlFor="title">Título del Proyecto *</Label>
              <Input
                id="title"
                value={formData.title}
                onChange={(e) => handleInputChange('title', e.target.value)}
                placeholder="Ej: Desarrollo de sitio web corporativo"
                disabled={isLoading}
              />
              {errors.title && (
                <Alert variant="destructive" className="mt-2">
                  <AlertDescription>{errors.title}</AlertDescription>
                </Alert>
              )}
            </div>

            <div>
              <Label htmlFor="description">Descripción *</Label>
              <Textarea
                id="description"
                value={formData.description}
                onChange={(e) => handleInputChange('description', e.target.value)}
                placeholder="Describe los detalles del proyecto..."
                rows={3}
                disabled={isLoading}
              />
              {errors.description && (
                <Alert variant="destructive" className="mt-2">
                  <AlertDescription>{errors.description}</AlertDescription>
                </Alert>
              )}
            </div>

            <div>
              <Label htmlFor="clientId">Cliente *</Label>
              <Select
                value={formData.clientId}
                onValueChange={(value) => handleInputChange('clientId', value)}
                disabled={isLoading}
              >
                <SelectTrigger>
                  <SelectValue placeholder="Seleccionar cliente" />
                </SelectTrigger>
                <SelectContent>
                  {clients.map((client) => (
                    <SelectItem key={client.id} value={client.id}>
                      {client.name}
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
              {errors.clientId && (
                <Alert variant="destructive" className="mt-2">
                  <AlertDescription>{errors.clientId}</AlertDescription>
                </Alert>
              )}
            </div>

            <div>
              <Label htmlFor="status">Estado</Label>
              <Select
                value={formData.status}
                onValueChange={(value) => handleInputChange('status', value)}
                disabled={isLoading}
              >
                <SelectTrigger>
                  <SelectValue />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="pending">Pendiente</SelectItem>
                  <SelectItem value="in-progress">En Progreso</SelectItem>
                  <SelectItem value="completed">Completado</SelectItem>
                  <SelectItem value="cancelled">Cancelado</SelectItem>
                </SelectContent>
              </Select>
            </div>
          </div>

          {/* Dates */}
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div>
              <Label htmlFor="startDate">Fecha de Inicio *</Label>
              <Input
                id="startDate"
                type="date"
                value={formData.startDate}
                onChange={(e) => handleInputChange('startDate', e.target.value)}
                disabled={isLoading}
              />
              {errors.startDate && (
                <Alert variant="destructive" className="mt-2">
                  <AlertDescription>{errors.startDate}</AlertDescription>
                </Alert>
              )}
            </div>

            <div>
              <Label htmlFor="endDate">Fecha de Fin</Label>
              <Input
                id="endDate"
                type="date"
                value={formData.endDate}
                onChange={(e) => handleInputChange('endDate', e.target.value)}
                disabled={isLoading}
              />
              {errors.endDate && (
                <Alert variant="destructive" className="mt-2">
                  <AlertDescription>{errors.endDate}</AlertDescription>
                </Alert>
              )}
            </div>
          </div>

          {/* Financial Information */}
          <div className="space-y-4">
            <div>
              <Label htmlFor="budget">Presupuesto (CLP)</Label>
              <Input
                id="budget"
                type="number"
                value={formData.budget}
                onChange={(e) => handleInputChange('budget', e.target.value)}
                placeholder="0"
                min="0"
                disabled={isLoading}
              />
              {errors.budget && (
                <Alert variant="destructive" className="mt-2">
                  <AlertDescription>{errors.budget}</AlertDescription>
                </Alert>
              )}
              {formData.budget && (
                <p className="text-sm text-slate-600 mt-1">
                  Presupuesto: {formatCLP(Number(formData.budget))}
                </p>
              )}
            </div>

            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div>
                <Label htmlFor="hoursWorked">Horas Trabajadas</Label>
                <Input
                  id="hoursWorked"
                  type="number"
                  value={formData.hoursWorked}
                  onChange={(e) => handleInputChange('hoursWorked', e.target.value)}
                  placeholder="0"
                  min="0"
                  step="0.5"
                  disabled={isLoading}
                />
                {errors.hoursWorked && (
                  <Alert variant="destructive" className="mt-2">
                    <AlertDescription>{errors.hoursWorked}</AlertDescription>
                  </Alert>
                )}
              </div>

              <div>
                <Label htmlFor="hourlyRate">Tarifa por Hora (CLP)</Label>
                <Input
                  id="hourlyRate"
                  type="number"
                  value={formData.hourlyRate}
                  onChange={(e) => handleInputChange('hourlyRate', e.target.value)}
                  placeholder="0"
                  min="0"
                  disabled={isLoading}
                />
                {errors.hourlyRate && (
                  <Alert variant="destructive" className="mt-2">
                    <AlertDescription>{errors.hourlyRate}</AlertDescription>
                  </Alert>
                )}
              </div>
            </div>

            {formData.hoursWorked && formData.hourlyRate && (
              <div className="p-3 bg-slate-50 rounded-lg">
                <p className="text-sm font-medium text-slate-700">
                  Valor Total por Horas: {formatCLP(calculateTotalValue())}
                </p>
                <p className="text-xs text-slate-500">
                  {formData.hoursWorked} horas × {formatCLP(Number(formData.hourlyRate))}
                </p>
              </div>
            )}
          </div>

          {/* Actions */}
          <div className="flex space-x-4 pt-4">
            <Button
              type="submit"
              disabled={isLoading}
              className="flex-1"
            >
              {isLoading ? (
                <div className="flex items-center space-x-2">
                  <div className="w-4 h-4 border-2 border-white border-t-transparent rounded-full animate-spin"></div>
                  <span>Guardando...</span>
                </div>
              ) : (
                project ? 'Actualizar Proyecto' : 'Crear Proyecto'
              )}
            </Button>
            
            <Button
              type="button"
              variant="outline"
              onClick={onCancel}
              disabled={isLoading}
              className="flex-1"
            >
              Cancelar
            </Button>
          </div>
        </form>
    </div>
  );
}
